// utils/videoProcessing.js
import fs from 'fs';
import path from 'path';
import axios from 'axios';
import { spawn } from 'child_process';
import { promisify } from 'util';
import { exec } from 'child_process';

const execPromise = promisify(exec);

export async function downloadVideo(videoUrl, outputPath) {
    try {
        console.log('Initiating video download from:', videoUrl);

        const response = await axios({
            method: 'GET',
            url: videoUrl,
            responseType: 'stream',
            timeout: 30000,
            validateStatus: status => status === 200
        });

        const contentType = response.headers['content-type'];
        const contentLength = response.headers['content-length'];

        console.log('Content Type:', contentType);
        console.log('Content Length:', contentLength);

        // Ensure temp directory exists
        const tempDir = path.dirname(outputPath);
        if (!fs.existsSync(tempDir)) {
            fs.mkdirSync(tempDir, { recursive: true });
        }

        const writer = fs.createWriteStream(outputPath);

        return new Promise((resolve, reject) => {
            let error = null;
            writer.on('error', err => {
                error = err;
                writer.close();
                reject(err);
            });

            writer.on('close', () => {
                if (!error) {
                    console.log('Video download completed successfully');
                    resolve(true);
                }
            });

            response.data.pipe(writer);
        });
    } catch (error) {
        console.error('Error in downloadVideo:', error);
        throw new Error(`Failed to download video: ${error.message}`);
    }
}

export async function transcribeVideo(videoPath) {
    console.log('Starting transcription for video:', videoPath);

    return new Promise((resolve, reject) => {
        // Use python script directly instead of whisper command
        const pythonScript = path.join(process.cwd(), 'transcribe_video.py');
        const pythonProcess = spawn('python', [pythonScript, videoPath]);

        let transcriptData = '';
        let errorData = '';

        pythonProcess.stdout.on('data', (data) => {
            const output = data.toString();
            console.log('Python output:', output);
            transcriptData += output;
        });

        pythonProcess.stderr.on('data', (data) => {
            const error = data.toString();
            console.error('Python error:', error);
            errorData += error;
        });

        pythonProcess.on('close', (code) => {
            console.log('Python process exited with code:', code);

            if (code === 0) {
                resolve(transcriptData.trim());
            } else {
                reject(new Error(`Transcription failed: ${errorData}`));
            }
        });
    });
}